import React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Header from './header_components/Header.js';
import About from './body_components/About.js';
import Projects from './body_components/Projects.js';
import Home from './body_components/Home.js';
import Contact from './body_components/Contact.js';
import Divider from '@material-ui/core/Divider';

function AppContainer() {
  return (
    <React.Fragment>
        <Header/>
        <Divider/>
        <Route exact path="/" component={Home} />
        <Route path="/about" component={About} />
        <Route exact path="/projects" component={Projects} />
        <Route path="/contact" component={Contact} />
    </React.Fragment>
  );
}


function App() {
  return (
      <Router>
        <div>
          <AppContainer />
        </div>
      </Router>
  );
}

export default App;
