import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Drawer from '@material-ui/core/Drawer';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import MenuIcon from '@material-ui/icons/Menu';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';

const NavButton = withStyles({
  root: {
    boxShadow: 'none',
    textTransform: 'none',
    borderRadius: '0',
    backgroundColor: 'transparent',
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
    '&:hover': {
      backgroundColor: '#b2dfdb',
    },
    '&:active': {
    },
    '&:focus': {
    },
  },
})(Button);

const useStyles = makeStyles(theme => ({
  grow: {
    flexGrow: 1,
  },
  title: {
    fontSize: 'initial',
    letterSpacing: 1,
    fontWeight: 'bold',
  },
  headerButton:{
    minHeight: 64,
    fontWeight:500,
    display:'flex',
    alignItems:'center',
    textDecoration: 'none',
    textTransform: 'uppercase',
    paddingLeft: 20,
    paddingRight: 20,
    '&:hover': {
      color: '#004d40',
    },
  },
  headerDrawer:{
    display: 'flex',
    flexDirection: 'column',
    minWidth: 300,
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
}));

export default function Header() {
  const classes = useStyles();

  const [state, setState] = React.useState({
    right: false,
  });

  const toggleDrawer = (side, open) => event => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
  
    setState({ ...state, [side]: open });
  };
  
  const sideList = side => (
    <div
      className={classes.headerDrawer}
      role="presentation"
      onClick={toggleDrawer(side, false)}
      onKeyDown={toggleDrawer(side, false)}
    >
    <NavButton>
      <Link className={classes.headerButton} to="/about">About</Link>
    </NavButton>
    <Divider />
    <NavButton>
      <Link className={classes.headerButton} to="/projects">Projects</Link>
    </NavButton>
    <Divider />
    <NavButton>
      <Link className={classes.headerButton} to="/contact">Contact</Link>
    </NavButton>
    <Divider />
    </div>
  );

  return (
    <div className={classes.grow}>
        <Toolbar disableGutters='true'>
          <NavButton>
            <Link className={classes.headerButton} to="/"><h1 className={classes.title}>Erich Showalter</h1></Link>
          </NavButton>
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            <NavButton>
              <Link className={classes.headerButton} to="/about">About</Link>
            </NavButton>
            <NavButton>
              <Link className={classes.headerButton} to="/projects">Projects</Link>
            </NavButton>
            <NavButton>
              <Link className={classes.headerButton} to="/contact">Contact</Link>
            </NavButton>
          </div>
          <div className={classes.sectionMobile}>
            <NavButton onClick={toggleDrawer('right', true)}><MenuIcon className={classes.headerButton} /></NavButton>
            <Drawer anchor="right" open={state.right} onClose={toggleDrawer('right', false)}>
              {sideList('right')}
            </Drawer>
          </div>
        </Toolbar>
    </div>
  );
}